.image-item
{
    color: #fff;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #fff;
    margin: 0 auto; 
    margin-bottom: 20px;
    margin-top: 4px;
    padding: 0px;
    display: block;
    text-align: center;
}

.image-item-left-div
{
    display: inline-block;
    height: 100%;
    background-color: #212121;
    border-bottom-right-radius: 10%;
    border-bottom-left-radius: 10%;
}

.image-item-img-div
{
    display: inline-block;
    margin: 0 auto;
    vertical-align:top;
    height: 100%;
    background-color: #212121;
}
.image-item-img
{
    width: 100%;
    max-width: 200px;
    margin: 0 auto;
}

.image-item-form
{
    vertical-align:top;
    display: inline-block;
    height: 100%;
    width: 59%;
    text-align: center;
    background-color: #212121;
}

.image-item-form-field-title
{
    text-align: center;
    margin: 0 auto;
    margin-top: 4px;
    padding: 0px;
    width: 100%;
    left: 0;
    font-size: 24px;
    font-family: 'Indie Flower', cursive;
}



.image-item-info
{
    max-width: 99%;
    width: 80%;
    min-height: 20px;
    text-align: center;
    border: none;
    background-color: #FF1744;
    background: -webkit-gradient(linear, left top, left bottom, from(#FF1744), to(#d50000));
    background: -webkit-linear-gradient(#FF1744, #d50000);
    background: -o-linear-gradient(#FF1744, #d50000);
    background: linear-gradient(#FF1744, #d50000); /* Standard syntax */
    color: #fff;
    margin-top: 4px;
    font-family: 'Ubuntu Condensed', sans-serif;
    border-radius: 3%;
   
}

.image-item-status 
{
    font-family: 'Ubuntu Condensed', sans-serif;
    width: 50px;
    margin-bottom: 10px;
}

.image-item-submit
{
    background-color: #FF1744;
    background: -webkit-gradient(linear, left top, left bottom, from(#FF1744), to(#d50000));
    background: -webkit-linear-gradient(#FF1744, #d50000);
    background: -o-linear-gradient(#FF1744, #d50000);
    background: linear-gradient(#FF1744, #d50000); /* Standard syntax */
    color: #fff;
    border:none;
    font-weight: bold;
    padding: 6px;
    margin-top: 10px;
    font-family: 'Ubuntu Condensed', sans-serif;
}

.image-item-label
{
    margin: 0 auto;
}

.image-item-category 
{
    width: 150px;
    text-align: center;
    border: none;
    background-color: #FF1744;
    background: -webkit-gradient(linear, left top, left bottom, from(#FF1744), to(#d50000));
    background: -webkit-linear-gradient(#FF1744, #d50000);
    background: -o-linear-gradient(#FF1744, #d50000);
    background: linear-gradient(#FF1744, #d50000); /* Standard syntax */
    color: #fff;
    margin-top: 10px;
    margin-right: 10px;
    font-family: 'Ubuntu Condensed', sans-serif;
    font-size: 15px;
}

.image-item-id
{
    position: absolute;
    display: inline-block;
    background-color: #FF1744;
    border-radius: 100%;
    width: 50px;
    height: 50px;
    border: #fff solid 2px;
    text-align: center;
    font-family: 'Luckiest Guy', cursive;
    font-size: 40px;
    margin-top: 10px;
    margin-left: 10px;
}


